body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.input-box{width:100%}.input-box .input-text{width:100%;height:44px;border-radius:3px;border:none;background-color:#f6f6f6;padding:0 15px;box-sizing:border-box}

.header{position:fixed;top:0;right:0;left:0;width:100%;background-color:#fff;border-bottom:1px solid #eee}.header .inner{display:flex;padding:20px 40px}.header .logo{margin-right:40px}.header .logo a{display:block}.header .logo img{height:42px}.header .gnb-list{display:flex;align-items:center}.header .gnb-list .list-item{margin-left:50px}.header .gnb-list .list-item a{font-size:20px}

.banner-area{height:270px;margin:50px 0 40px;background-image:url(/banner-sneakers.jpg);background-repeat:no-repeat;background-size:cover}.banner-area .inner{padding:100px 50px 0}.banner-area .banner-title{display:block;margin-bottom:20px;font-size:52px;color:#000}.banner-area .banner-text{font-size:18px;color:#000;line-height:1.25}

.lnb{position:absolute}

.product-area{margin-bottom:50px}.product-list{overflow:hidden;display:flex;flex-direction:row;flex-wrap:wrap;margin:0 -10px}.product-list .list-item{width:25%;padding:10px;box-sizing:border-box}.product-list .list-item:first-child{margin-left:0}.product-list .list-box{border:1px solid #eee}.product-list .product-img img{width:100%}.product-list .product-info{min-height:84px;padding:15px;background-color:#fafafa;border-top:1px solid #eee;text-align:left;box-sizing:border-box}.product-list .product-name{display:block;min-height:36px;font-size:16px;overflow:hidden;text-overflow:ellipsis;display:-webkit-box;-webkit-line-clamp:2;-webkit-box-orient:vertical}.product-list .product-price-info{display:block;margin:5px 0 10px}.product-list .product-price{font-size:22px;font-weight:bold;color:#000}

.content{width:1250px;margin:0 auto;padding:85px 40px 0;box-sizing:border-box}

.App{min-width:1250px}

body,p,h1,h2,h3,h4,h5,h6,ul,ol,li,dl,dt,dd,table,th,td,form,fieldset,legend,input,textarea,button,select{margin:0;padding:0}html,body{width:100%;height:100%}body,input,textarea,select,button,table{color:#505050;font-family:'Noto Sans', 'Noto Sans KR', Helvetica, 'Microsoft YaHei', 'Apple SD Gothic Neo', 'Malgun Gothic', '맑은 고딕', Dotum, 돋움, sans-serif;font-size:13px;line-height:18px}h1,h2,h3,h4,h5,h6{font-size:inherit;line-height:inherit}textarea{-webkit-backface-visibility:hidden;backface-visibility:hidden;background-color:transparent;border:0;word-break:keep-all;word-wrap:break-word}textarea:focus{outline:none}input,button{border:0;border-radius:0}input:focus,button:focus{outline:none}button{background-color:transparent;cursor:pointer}img,fieldset{border:0}img{vertical-align:top}ul,ol{list-style:none}em,address{font-style:normal}a{color:inherit;text-decoration:none}iframe{border:0;margin:0;overflow:hidden;padding:0}mark{background-color:transparent}i{font-style:normal}:global #root{height:100%}

